import React from "react";
import NProgress from "nprogress";
import styled from "styled-components";

import MuffynLogoSvg from "../components/MuffynLogoSvg";
import Title from "../components/Title";
import Button from "../components/Button";

const Container = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
`;

const StyledP = styled.p`
  line-height: 1.4em;
  max-width: 300px;
`;

const Cta = styled.footer`
  margin-top: 3em;
`;

const FileInput = styled.input`
  height: 0;
  overflow: hidden;
  width: 0;
`;

const FileInputLabel = styled.label`
  border: 2px solid white;
  border-radius: 2px;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-size: 0.8em;
  letter-spacing: 0.09em;
  outline: none;
  padding: 1em 1.4em 1em 1.6em;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.2s;
  &:hover {
    box-shadow: inset 0 0 0 3px white;
  }
`;

const Point = styled.span`
  display: inline-block;
  font-size: 0.8em;
  margin-left: 3px;
`;

const CtaFooter = props => (
  <Cta>
    <Button text="Generate Another Avatar" url="/avatar" />{" "}
    <Button text="Festival Home" url="/" />
  </Cta>
);

class CreateAvatarPage extends React.Component {
  state = {
    image: null
  };

  processAvatar = async e => {
    const files = e.target.files;
    const data = new FormData();
    data.append("file", files[0]);
    data.append("upload_preset", "muffyn-avatar-overlay");

    const res = await fetch(
      "https://api.cloudinary.com/v1_1/bronycon/image/upload",
      {
        method: "POST",
        body: data
      }
    );
    const file = await res.json();
    this.setState({
      image: file.secure_url
    });
  };

  render() {
    return (
      <Container>
        <Title>Avatar Creater</Title>
        <StyledP>
          Spread the muffin by adding one to your social media avatar.
        </StyledP>
        <form
          onSubmit={async e => {
            e.preventDefault();

            // Router.push({
            //   pathname: '/item',
            //   query: { id: res.data.createItem.id },
            // });
          }}
        >
          <FileInput
            type="file"
            id="file"
            name="file"
            required
            onChange={this.processAvatar}
          />
          <FileInputLabel for="file">
            Select A Photo <Point />
          </FileInputLabel>
        </form>
        {this.state.image && <img src={this.state.image} />}
        <CtaFooter />
      </Container>
    );
  }
}

export default CreateAvatarPage;
